import { h } from 'preact';
// import { Link } from 'preact-router/match';
import Link from 'react-router-dom/Link';
import style from './style';

const Header = () => (
	<header class={style.header}>
		<h1>Preact App</h1>
		<nav>
			<Link activeClassName={style.active} to="/">Home</Link>
			<Link activeClassName={style.active} to="/profile">Me</Link>
			<Link activeClassName={style.active} to="/profile/john">John</Link>
			<Link activeClassName={style.active} to="/github">Github</Link>
		</nav>
	</header>
);

export default Header;



